export default {
  // 父子通信
  props: {
    postObj: {
      type: Object,
      default: function(){
      	return {};
      }
    },
	showPostOpt: {
      type: Boolean,
      default: true,
    },
  },
  components : {
  },
  data () {
    return {

    };
  },
  mounted () {

  },
  computed: {
    isLogin () {
	  return true;
    },
  },
  methods: {
    // 点击分享
    sharePost (item) {
      let sharePopShow = true;
      let itemIsTop = true;
      this.$emit('on-share-post', sharePopShow, itemIsTop)
    },
    // 点击评论
    openCommentsPop (item) {
      this.$emit('on-open-comments-popup', item)
    },
    // 加入喜欢
    addMyLike: function (item) {
      let that = this;
      if(that.isLogin){
        if (item.isLike) {
          item.isLike = false;
          item.likers -= 1;
        }else{
          item.isLike = true;
          item.likers += 1;
          that.$toast('成功收藏！');
        }
      }else {
        that.$dialog.confirm({
          title: '未登录',
          message: '登录后可收藏至您的喜欢',
          confirmButtonText: '立即登录'
        }).then(() => {
          that.$router.push({  //核心语句
            path:'/login'   //跳转的路径
          })
        }).catch(() => {
          // on cancel
        });
      }
    },
    // 加关注
    addFollow: function (item) {
      let that = this;
	  const query = that.Bmob.Query('postList')
	  query.get(item.objectId).then(res => {
	      res.increment('view')
	      res.save()
	  }).catch(err => {
	      console.log(err)
	  })
      // if(that.isLogin){
      //   if (item.isFollow === 1) {
      //     item.isFollow = 0;
      //     that.$toast('关注成功！');
      //   }
      // }else {
      //   that.$router.push({  //核心语句
      //     path:'/login'   //跳转的路径
      //   })
      // }
    },
    // 帮顶
    addPraise: function (item) {
      let that = this;
      if(that.isLogin){
        if (item.isPraised === 1) {
          item.isPraised = 0;
          that.$toast('帮顶成功，将会有更多的人看到哦！');
        }
      }else {
        that.$router.push({  //核心语句
          path:'/login'   //跳转的路径
        })
      }
    },
    // 图片预览
    preImg(index) {
    	uni.previewImage({
    		current: this.postObj.images[index],
    		urls: this.postObj.images
    	})
    },
    // 前往用户主页
    toUserZone () {
      this.$router.push({ name: 'userzone', params: '' })
    },
    // 前往帖子分类
    toPostClassify (item) {
      this.$router.push({ name: 'postClassify', params: { containerid: item.containerid }})
    },
    // 前往顶赞用户列表
    toTopPraiseUserList (item) {
      this.$router.push({ name: 'topPraiseUserList', params: { postId: item.postId }})
    },
  }
};
